import Image from "next/image";
import { useState } from "react";
import { FaTrashAlt } from "react-icons/fa";
import Counter from "../Counter";

interface CartItemProps {
	id: string;
	name: string;
	price: number;
	size?: string;
	color?: string;
	imageUrl: string;
	initialQuantity?: number;
	onRemove?: (id: string) => void;
	onQuantityChange?: (id: string, newQuantity: number) => void;
}

export default function CartItem({
	id,
	name,
	price,
	size = "M",
	color = "Preto",
	imageUrl,
	initialQuantity = 1,
	onRemove,
	onQuantityChange,
}: CartItemProps) {
	const [quantity, setQuantity] = useState(initialQuantity);

	const handleQuantityChange = (newQuantity: number) => {
		setQuantity(newQuantity);
		if (onQuantityChange) {
			onQuantityChange(id, newQuantity);
		}
	};

	return (
		<div className="flex items-start gap-4 py-4 border-b border-gray-200">
			{/* Imagem do produto */}
			<div className="relative w-[99px] h-[99px] flex-shrink-0 bg-gray-100 rounded-md overflow-hidden">
				<Image
					src={imageUrl || "/placeholder-product.jpg"}
					alt={name}
					fill
					className="object-contain"
				/>
			</div>

			{/* Informações do produto */}
			<div className="flex-1 min-w-0">
				{/* Nome e botão de remover */}
				<div className="flex justify-between items-start mb-1">
					<h3 className="font-medium text-base line-clamp-2 pr-2">{name}</h3>
					<button
						onClick={() => onRemove && onRemove(id)}
						className="text-gray-400 hover:text-red-500 transition-colors p-1"
						aria-label="Remover item"
					>
						<FaTrashAlt className="text-red-600" size={16} />
					</button>
				</div>

				{/* Tamanho e cor */}
				<div className="text-sm text-gray-600 space-y-1 mb-3">
					{size && <p>Tamanho: {size}</p>}
					{color && <p>Cor: {color}</p>}
				</div>

				{/* Preço e contador */}
				<div className="flex justify-between items-center">
					<span className="font-semibold text-base">
						R$ {price.toFixed(2).replace(".", ",")}
					</span>
					<Counter
						value={quantity}
						onChange={handleQuantityChange}
						min={1}
						max={99}
					/>
				</div>
			</div>
		</div>
	);
}
