"use client";
import Header from "@/components/Header";
import CartItem from "@/components/products/CartItem";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import {
	Breadcrumb,
	BreadcrumbItem,
	BreadcrumbLink,
	BreadcrumbList,
	BreadcrumbPage,
	BreadcrumbSeparator,
} from "@/components/ui/breadcrumb";
import { FaArrowRight } from "react-icons/fa";
import { useState } from "react";

// Dados mockados do carrinho
const mockCartItems = [
	{
		id: "prod-1",
		name: "Camiseta Básica Branca",
		price: 49.9,
		size: "M",
		color: "Branco",
		imageUrl: "/camiseta-branca.jpg",
		quantity: 2,
	},
	{
		id: "prod-2",
		name: "Calça Jeans Slim",
		price: 129.9,
		size: "42",
		color: "Azul Escuro",
		imageUrl: "/calca-jeans.jpg",
		quantity: 1,
	},
	{
		id: "prod-3",
		name: "Tênis Esportivo",
		price: 199.9,
		size: "41",
		color: "Preto",
		imageUrl: "/tenis-esportivo.jpg",
		quantity: 1,
	},
];

export default function Cart() {
	const [cartItems, setCartItems] = useState(mockCartItems);
	const [couponCode, setCouponCode] = useState("");
	const [discount, setDiscount] = useState(0);

	// Calcular valores totais
	const subtotal = cartItems.reduce(
		(sum, item) => sum + item.price * item.quantity,
		0
	);
	const shippingFee = subtotal > 200 ? 0 : 15.9;
	const total = subtotal + shippingFee - discount;

	// Remover item do carrinho
	const handleRemoveItem = (id: string) => {
		setCartItems(cartItems.filter((item) => item.id !== id));
	};

	// Atualizar quantidade de um item
	const handleQuantityChange = (id: string, newQuantity: number) => {
		setCartItems(
			cartItems.map((item) =>
				item.id === id ? { ...item, quantity: newQuantity } : item
			)
		);
	};

	// Aplicar cupom de desconto
	const applyCoupon = () => {
		if (couponCode.toUpperCase() === "DESCONTO10") {
			setDiscount(subtotal * 0.1); // 10% de desconto
		} else {
			setDiscount(0);
			alert("Cupom inválido ou expirado");
		}
	};

	return (
		<main className="min-h-screen flex flex-col">
			<Header />

			{/* Breadcrumb */}
			<div className="container mx-auto px-4 py-4">
				<Breadcrumb>
					<BreadcrumbList>
						<BreadcrumbItem>
							<BreadcrumbLink href="/">Home</BreadcrumbLink>
						</BreadcrumbItem>
						<BreadcrumbSeparator />
						<BreadcrumbItem>
							<BreadcrumbPage>Carrinho</BreadcrumbPage>
						</BreadcrumbItem>
					</BreadcrumbList>
				</Breadcrumb>
			</div>

			{/* Conteúdo principal */}
			<div className="container mx-auto px-4 py-6 flex-1 flex flex-col md:flex-row gap-8">
				{/* Seção de itens do carrinho */}
				<section className="md:w-2/3">
					<h1 className="text-2xl font-bold mb-6">
						Seu Carrinho ({cartItems.length})
					</h1>

					{cartItems.length === 0 ? (
						<div className="text-center py-12">
							<div className="bg-gray-100 border-2 border-dashed rounded-xl w-16 h-16 mx-auto mb-4 flex items-center justify-center">
								<svg
									xmlns="http://www.w3.org/2000/svg"
									width="24"
									height="24"
									viewBox="0 0 24 24"
									fill="none"
									stroke="currentColor"
									strokeWidth="2"
									strokeLinecap="round"
									strokeLinejoin="round"
								>
									<circle cx="8" cy="21" r="1"></circle>
									<circle cx="19" cy="21" r="1"></circle>
									<path d="M2.05 2.05h2l2.66 12.42a2 2 0 0 0 2 1.58h9.78a2 2 0 0 0 1.95-1.57l1.65-7.43H5.12"></path>
								</svg>
							</div>
							<h2 className="text-xl font-semibold mb-2">
								Seu carrinho está vazio
							</h2>
							<p className="text-gray-600 mb-6">
								Adicione produtos para continuar
							</p>
							<Button asChild>
								<a href="/products">Continuar Comprando</a>
							</Button>
						</div>
					) : (
						<div className="bg-white rounded-lg shadow-sm border">
							{cartItems.map((item) => (
								<CartItem
									key={item.id}
									id={item.id}
									name={item.name}
									price={item.price}
									size={item.size}
									color={item.color}
									imageUrl={item.imageUrl}
									initialQuantity={item.quantity}
									onRemove={handleRemoveItem}
									onQuantityChange={handleQuantityChange}
								/>
							))}

							<div className="p-4 border-t flex justify-end">
								<Button variant="outline" asChild>
									<a href="/products">Continuar Comprando</a>
								</Button>
							</div>
						</div>
					)}
				</section>

				{/* Seção de resumo da compra */}
				{cartItems.length > 0 && (
					<section className="md:w-1/3">
						<div className="bg-white rounded-lg shadow-sm border p-6 sticky top-6">
							<h2 className="text-xl font-bold mb-6">Resumo da Compra</h2>

							<div className="space-y-4 mb-6">
								<div className="flex justify-between">
									<span>Subtotal</span>
									<span>R$ {subtotal.toFixed(2).replace(".", ",")}</span>
								</div>

								<div className="flex justify-between">
									<span>Desconto</span>
									<span className="text-green-600">
										- R$ {discount.toFixed(2).replace(".", ",")}
									</span>
								</div>

								<div className="flex justify-between">
									<span>Taxa de Entrega</span>
									<span>
										{shippingFee > 0
											? `R$ ${shippingFee.toFixed(2).replace(".", ",")}`
											: "Grátis"}
									</span>
								</div>

								<div className="h-px bg-gray-200 my-2"></div>

								<div className="flex justify-between font-bold text-lg">
									<span>Total</span>
									<span>R$ {total.toFixed(2).replace(".", ",")}</span>
								</div>
							</div>

							<div className="mb-6">
								<label
									className="block text-sm font-medium mb-2"
									htmlFor="coupon"
								>
									Adicionar cupom
								</label>
								<div className="flex gap-2">
									<Input
										id="coupon"
										placeholder="Código do cupom"
										value={couponCode}
										onChange={(e) => setCouponCode(e.target.value)}
									/>
									<Button
										variant="secondary"
										onClick={applyCoupon}
										disabled={!couponCode.trim()}
									>
										Aplicar
									</Button>
								</div>
							</div>

							<Button
								className="w-full bg-green-600 hover:bg-green-700 h-12 text-lg"
								asChild
							>
								<a href="/cart/checkout">
									Finalizar Compra
									<FaArrowRight className="ml-2" />
								</a>
							</Button>

							{subtotal > 0 && subtotal < 200 && (
								<div className="mt-4 text-center text-sm text-green-600 bg-green-50 p-2 rounded">
									Adicione mais R${" "}
									{(200 - subtotal).toFixed(2).replace(".", ",")} para frete
									grátis!
								</div>
							)}
						</div>
					</section>
				)}
			</div>
		</main>
	);
}
